import * as Yup from "yup";

export const registrationSchema = Yup.object({
  email: Yup.string().email().required("Email id is required"),
  first: Yup.string().min(2).max(20).required("First name is required"),
  last: Yup.string().min(2).max(20).required("Last name is required"),
  mobileNumbr: Yup.string().min(10).required("Please enter your Mobile Number"),
  confirMobileNumber: Yup.string()
    .required("Confirm Mobile Number is required")
    .oneOf([Yup.ref("mobile"), null], "mobile number should must match"),
});
